<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="3">
          <v-img
            :src="require('../../assets/icons/calculator03.png')"
            contain
            height="90"
          />
        </v-col>

        <v-col cols="8" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title">
            ¿Cómo calcular el IGV?
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col>
          <!-- https://feelingperu.com/calculadora-igv-peru/ -->
          <p class="text-center">
            El cálculo de IGV se hace aplicando el 18% en caso de tener el
            importe base, por ejemplo: IGV = Importe Base x 0.18 En caso de
            saber el importe total es decir incluido IGV entonces el cálculo
            sería así: Primero obtenemos el monto base de la siguiente manera:
            Base = Total / (1 + 0.18) o Base = Total / 1.18 Finalmente restando
            el monto total menos el monto base nos da como resultado el IGV: IGV
            = Total – Base Enseguida se mostrará el equivalente en los otros dos
            valores, y si utiliza nuestra calculadora arriba le genera la el
            resultado de escritura en texto y la fecha del sistema para el
            llenado correcto de una factura.
          </p>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <div class="call-to-action" style="margin: 1rem">
            <p>Monto Base</p>
            <v-text-field
              type="number"
              @keyup="calculateEvent('monto_base')"
              v-model="price.monto_base"
              prefix="S/"
              hide-details
            ></v-text-field>
          </div>
          <div class="call-to-action" style="margin: 1rem">
            <p>IGV 18%</p>
            <v-text-field
              type="number"
              @keyup="calculateEvent('monto_igv')"
              v-model="price.monto_igv"
              prefix="S/"
              hide-details
            ></v-text-field>
          </div>
          <div class="call-to-action" style="margin: 1rem">
            <p>Total</p>
            <v-text-field
              type="number"
              @keyup="calculateEvent('monto_total')"
              v-model="price.monto_total"
              prefix="S/"
              hide-details
            ></v-text-field>
          </div>
        </v-col>
        <v-col cols="9"></v-col>
        <v-col cols="3" class="text-center" @click="goToIndex">
          <v-img
            :src="require('../../assets/icons/home-12.svg')"
            contain
            height="48"
          />
          Regresar
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Calculator03",
  data: () => ({
    uit: 0,
    price: {
      monto_base: 0,
      monto_igv: 0,
      monto_total: 0,
    },
    tax: 0,
  }),
  computed: {
    total() {
      return (
        Number(this.price.monto_base) +
        Number(this.price.monto_igv) +
        Number(this.price.monto_total)
      );
    },
  },
  mounted() {
    this.getUITValue();
  },
  methods: {
    getUITValue() {
      let url = "settings/1";
      axios.get(url).then((response) => {
        this.uit = parseInt(response.data.value);
      });
    },

    onSubmit() {
      this.tax = (this.price.auto - this.price.total) * 0.05;

      if (this.tax <= 0) {
        this.tax = "No se encuentra afecto";
      }
    },
    goToIndex() {
      this.$router.push({ name: "CalculatorIndex" });
    },

    getRedondear(num) {
      return Math.round(num * 100) / 100
    },
    calculateEvent: function (evt) {
      if (evt == "monto_igv") {
        this.price.monto_base  = this.getRedondear((Number(this.price.monto_igv) *100)/ 18);
        this.price.monto_total = this.getRedondear(Number(this.price.monto_igv)  +(Number(this.price.monto_igv) *100)/ 18);
      } 
      else if (evt == "monto_base") {
        this.price.monto_igv  = this.getRedondear(Number(this.price.monto_base) * 0.18);
        this.price.monto_total = this.getRedondear(Number(this.price.monto_base)  + Number(this.price.monto_base) * 0.18);
      }
      else if (evt == "monto_total") {
      this.price.monto_base  = this.getRedondear(Number(this.price.monto_total)/ 1.18);
      this.price.monto_igv  = this.getRedondear((Number(this.price.monto_total)/ 1.18)* 0.18);
      }
    },
  },
};
</script>
